import * as React from 'react'
import { AppBar, Toolbar } from '@material-ui/core'

import SectionButton from '../components/index/section_button'
import Profile from '../components/index/profile/profile'
import Skills from '../components/index/skills/skills'
import Projects from '../components/index/projects/projects'
import Contact from '../components/index/contact/contact'

import { useClientRect } from '../components/hooks/useClientRect'

import '../styles/global.css'

// markup
const IndexPage = () => {
  const [headerRect, headerRef] = useClientRect()
  const [sectionHeight, setSectionHeight] = React.useState(0)

  React.useEffect(() => {
    setSectionHeight(document.documentElement.clientHeight)
  }, [])

  const sectionList = [
    { name: 'Profile', component: Profile },
    { name: 'Skills', component: Skills },
    { name: 'Projects', component: Projects },
    { name: 'Contact', component: Contact },
  ]

  return (
    <div>
      <AppBar
        ref={headerRef}
        position="fixed"
        color="transparent"
        elevation={0}
        style={{
          backdropFilter: 'blur(4px)',
        }}
      >
        <Toolbar>
          {sectionList.map((section, idx) => (
            <SectionButton key={idx} delay={idx * 200} to={section.name} />
          ))}
        </Toolbar>
      </AppBar>
      {sectionList.map(section => (
        <section.component style={{ height: sectionHeight }} />
      ))}
    </div>
  )
}

export default IndexPage
